<template>
    <div id="good-tree">
        <!-- <div class="tree-title">商品列表</div> -->
        <div class="tree-content">
          <z-tree ref="tree" 
          @node-click="selectNodeActive" 
          @delete-category="deleteCategorys" 
          @editor-category="openEditorDialog" 
          :treeData="treeData"
          @add-category="addCategorys"></z-tree>
        </div>

        <!-- 商品添加弹窗 -->
        <el-dialog class="category-dialog" :visible.sync="addDialogVisible" :show-close="false" size="" :before-close="handleClose"> 
            <div class="dialog-title" slot="title">添加商品分类</div>
            <div class="dialog-content">
                <div class="category clearfix">
                    <div class="content-left left">
                        <span>分类名称</span>
                    </div>
                    <div class="content-right left">
                        <el-input class="left" v-model="dialongInputValue" placeholder="请输入内容"></el-input>
                    </div>
                </div>

                <div class="button-group clearfix">
                    <div class="ok-btn left" @click="dialogAddFirst">确定</div>
                    <div class="cancel-btn left" @click="dialogAddCancelFirst">取消</div>
                </div>
            </div>
        </el-dialog>

        <!-- 添加分类弹窗 -->
        <el-dialog class="category-dialog" 
        :visible.sync="EditorDialogVisible" 
        :show-close="false" size="" 
        :modal-append-to-body="false"
        :before-close="handleClose">
            <div class="dialog-title" slot="title">{{ dialogTitle }}分类</div>
            <div class="dialog-content">
                <div class="dinner-time clearfix">
                    <div class="content-left left">
                        <span>所属餐时</span>
                    </div>

                    <el-checkbox-group :min="1" class="left content-right" v-model="dinnerTimeArr">
                        <el-checkbox-button v-for="item in dinnerTimeSelect" :label="item.value" :key="item.value">{{item.label}}</el-checkbox-button>
                    </el-checkbox-group>
                </div>

                <div class="dinner-tip">
                    注：默认全选，仅可设置上级目录包含的属于餐时项
                </div>

                <div class="category clearfix">
                    <div class="content-left left">
                        <span>分类名称</span>
                    </div>
                    <div class="content-right left">
                        <el-input 
                        class="left" 
                        v-model="dialongInputValue" 
                        placeholder="请输入内容"
                        :class="{errtip:isShowErrtip}"
                        @blur="checkCataname"
                        @change="clearCateNameTip"></el-input>
                    </div>
                </div>
                <div class="catename-tipcontent">
                  <div v-show="isShowErrtip" class="catename-tip">分类名称不得超过10个字</div>
                </div>

                <div v-if="isEditor" class="button-group clearfix">
                    <div class="ok-btn left" @click="editorCategorys">确定</div>
                    <div class="cancel-btn left" @click="dialogAddCancel">取消</div>
                </div>

                <div v-else class="button-group clearfix">
                    <div class="ok-btn left" @click="dialogAdd">确定</div>
                    <div class="cancel-btn left" @click="dialogAddCancel">取消</div>
                </div>
            </div>
        </el-dialog>

        <!-- 删除提示弹窗 -->
        <el-dialog class="delete-dialog" 
        :visible.sync="DeleteDialogVisible" 
        :show-close="false" 
        size="" 
        :before-close="handleClose"
        :modal-append-to-body="false"
        >
            <div class="dialog-title" slot="title"></div>
            <div class="dialog-content">
                <div class="delete-tip">
                    <div class="delete-img">
                        <img src="./images/question.png" alt="提示图片">
                    </div>
                    <div class="delete-text">删除此商品分类将会删除它的子分类，是否删除？</div>
                </div>
                <div class="button-group clearfix">
                    <div class="ok-btn left" @click="dialogDelete">确定</div>
                    <div class="cancel-btn left" @click="dialogDeleteCancel">取消</div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import ZTree from "../components/tree/tree.vue";
import { GoodList } from "../index";
import { getTableCategory, editorCategory } from "../api";
import Vue from "vue";
import { GoodType, Dinnertime } from "@/config/cfg";

export default {
  data() {
    return {
      addDialogVisible: false,
      EditorDialogVisible: false,
      DeleteDialogVisible: false,
      isEditor: false, //是编辑弹框
      dialogTitle: "",
      dialongInputValue: "",
      dinnerTimeData: [
        {
          label: Dinnertime.toString(Dinnertime.MORN),
          value: Dinnertime.MORN
        },
        {
          label: Dinnertime.toString(Dinnertime.NOON),
          value: Dinnertime.NOON
        },
        {
          label: Dinnertime.toString(Dinnertime.EVEN),
          value: Dinnertime.EVEN
        },
        {
          label: Dinnertime.toString(Dinnertime.NIGHT),
          value: Dinnertime.NIGHT
        }
      ],
      dinnerTimeSelect: [],
      dinnerTimeArr: [], //当前选中的时段
      treeData: [],
      treeDataById: null,
      dialogParentId: null,
      dialogNode: null, //弹窗分类节点
      dinnerSelected: null,
      goodType: GoodType,

      isShowErrtip: false
    };
  },
  computed: {
    selectCategoryId() {
      return this.$store.state.good.selectCategoryId;
    }
  },
  components: {
    ZTree
  },
  watch: {
    treeData: {
      handler: function(val) {
        this.treeDataByIds();
      },
      deep: true
    }
  },
  created() {
    this.getCategory();
  },
  methods: {
    //选中样式
    selectNodeActive(node) {
      this.$store.commit("CHANGE_ID", node.category_id);
      this.$store.commit("CHANGE_TYPE", node.type);
      GoodList.changeSelecte(node, this.treeDataById);
    },
    treeDataByIds() {
      this.treeDataById = GoodList.treeDataById(this.treeData);
    },
    //拉取分类
    getCategory() {
      getTableCategory(resp => {
        if (resp.ret === 0) {
          let data = resp.data.list;
          let treeDataObj = GoodList.addCategoryFirst(data);
          Vue.set(this.treeData, 0, treeDataObj);
          GoodList.generateKey(this.treeData, 0);
          GoodList.initTreeData(this.treeData);
          GoodList.selectIcon(this.treeData);
        } else {
          console.warn("tree结构分类获取失败！");
        }
      });
    },

    //编辑
    openEditorDialog(node) {
      this.dialogNode = node;
      this.dialogTitle = "修改" + node.category_name;
      this.dialogParentId = node.parent_id;
      this.dinnerTimeArr = node.opening_time;
      this.dialongInputValue = node.category_name;
      this.EditorDialogVisible = true;
      this.isEditor = true;
      this.changeSelecteDinnerParent();

      this.clearCateNameTip();
    },
    // 编辑保存
    editorCategorys() {
      if (this.dialongInputValue === "") {
        this.$notify({
          title: "警告",
          message: "输入不能为空",
          type: "warning"
        });
        return;
      }
      let data = {
        save: 1,
        category_id: this.dialogNode.category_id,
        category_name: this.dialongInputValue,
        parent_id: this.dialogNode.parent_id,
        opening_time: this.dinnerTimeArr
      };
      editorCategory(data, resp => {
        if (resp.ret === 0) {
          this.getCategory();
          GoodList.selectIcon(this.treeData);
          this.EditorDialogVisible = false;
          this.isEditor = false;
          this.dialongInputValue = "";
          console.log("修改成功");
        } else {
          this.EditorDialogVisible = false;
          this.isEditor = false;
          this.dialongInputValue = "";
          console.warn("菜品分类修改失败！");
        }
      });
    },

    changeSelecteDinnerParent() {
      let node = this.treeDataById[this.dialogParentId];
      this.dinnerTimeSelect = GoodList.selectdinnerTime(
        this.dinnerTimeData,
        node.opening_time
      );
    },

    //商品分类增加
    dialogAddFirst() {
      if (this.dialongInputValue === "") {
        this.$notify({
          title: "警告",
          message: "输入不能为空",
          type: "warning"
        });
        return;
      }

      let data = {
        save: 1,
        category_name: this.dialongInputValue,
        opening_time: [1, 2, 3, 4],
        type: GoodType.DISH
      };
      editorCategory(data, resp => {
        if (resp.ret === 0) {
          this.getCategory();
          this.addDialogVisible = false;
          this.dialongInputValue = "";
          console.log("添加成功");
        } else {
          this.addDialogVisible = false;
          this.dialongInputValue = "";
          console.warn("商品分类添加失败");
        }
      });
    },

    dialogAddCancelFirst() {
      this.addDialogVisible = false;
    },

    //打开分类增加弹窗
    addCategorys(node) {
      this.dialogNode = node;
      this.dinnerTimeArr = node.opening_time;

      //当前需要渲染的餐时
      this.dinnerTimeSelect = GoodList.selectdinnerTime(
        this.dinnerTimeData,
        this.dialogNode.opening_time
      );

      if (node.category_id === "0") {
        this.addDialogVisible = true;
      } else {
        this.dialogTitle = "添加" + node.category_name;
        this.EditorDialogVisible = true;
      }

      this.clearCateNameTip();
    },
    //分类增加
    dialogAdd() {
      if (this.dialongInputValue === "") {
        this.$notify({
          title: "警告",
          message: "输入不能为空",
          type: "warning"
        });
        return;
      }
      let data = {
        save: 1,
        category_name: this.dialongInputValue,
        opening_time: this.dinnerTimeArr,
        parent_id: this.dialogNode.category_id,
        type: this.dialogNode.type || 1
      };
      editorCategory(data, resp => {
        if (resp.ret === 0) {
          this.getCategory();
          GoodList.selectIcon(this.treeData);
          this.EditorDialogVisible = false;
          this.isEditor = false;
          this.dialongInputValue = "";
          console.log("修改成功");
        } else {
          this.EditorDialogVisible = false;
          this.isEditor = false;
          this.dialongInputValue = "";
          console.warn("菜品分类添加失败!");
        }
      });
    },
    dialogAddCancel() {
      this.EditorDialogVisible = false;
      this.isEditor = false;
      this.dialongInputValue = "";
    },

    //分类删除
    deleteCategorys(node) {
      this.dialogNode = node;
      this.DeleteDialogVisible = true;
    },
    dialogDelete() {
      let data = {
        delete: 1,
        category_id: this.dialogNode.category_id
      };

      editorCategory(data, resp => {
        if (resp.ret === 0) {
          this.getCategory();
          GoodList.selectIcon(this.treeData);
          this.DeleteDialogVisible = false;
          console.log("删除成功");
        } else {
          this.DeleteDialogVisible = false;
          console.warn("菜品分类删除失败");
        }
      });
    },
    dialogDeleteCancel() {
      this.DeleteDialogVisible = false;
    },

    //弹窗关闭前回调
    handleClose() {
      this.EditorDialogVisible = false;
      this.isEditor = false;
      this.dialongInputValue = "";
      this.addDialogVisible = false;
      this.EditorDialogVisible = false;
      this.DeleteDialogVisible = false;
    },
    checkCataname() {
      if (this.dialongInputValue.length > 10) {
        this.isShowErrtip = true;
      }
    },
    clearCateNameTip() {
      this.isShowErrtip = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#good-tree {
  background: #ffffff;
  //   box-shadow: 0 2px 4px 0 #becaeb;
}

.tree-title {
  @include wh(100%, 40px);
  @include fc(14px, #666666);
  line-height: 40px;
  background-color: #f6f8fc;
  padding-left: 14px;
  margin-bottom: 40px;
}
.tree-content {
  max-height: 552px;
  overflow-y: scroll;
}
.category-dialog {
  .dialog-title {
    @include wh(540px, 40px);
    @include fc(16px, #fff);
    background-color: #5a8cff;
    text-align: center;
    line-height: 40px;
  }

  .dialog-content {
    padding: 50px 80px;

    .dinner-time {
      .content-left {
        @include wh(80px, 30px);
        line-height: 30px;
      }
      .content-right {
        @include wh(300px, 30px);

        li {
          @include wh(60px, 30px);
          border: 1px solid #d8d8d8;
          line-height: 30px;
          text-align: center;
          color: #666;
          cursor: pointer;
          margin-right: 10px;
        }

        li.active {
          @include wh(60px, 30px);
          @include fc(14px, #5a8cff);
          line-height: 30px;
          text-align: center;
          border: 1px solid #5a8cff;
          margin-right: 10px;
          position: relative;
          margin-bottom: 10px;
        }

        li.active::after {
          @include wh(12px, 12px);
          position: absolute;
          content: "";
          background-image: url(./images/select.png);
          background-repeat: no-repeated;
          top: 0;
          right: 0;
        }
      }
    }

    .category {
      .content-left {
        @include wh(80px, 34px);
        line-height: 34px;
      }

      .content-right {
        @include wh(300px, 34px);
      }
    }

    .button-group {
      margin-top: 14px;

      div {
        @include wh(160px, 40px);
        border-radius: 4px;
        border: 1px solid #5a8cff;
        color: #5a8cff;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
      .ok-btn {
        margin-right: 60px;
        background-color: #5a8cff;
        color: #fff;
      }
    }
  }

  .dinner-tip {
    @include fc(12px, #9b9b9b);
    margin-left: 80px;
    padding: 10px 0 20px 0;
  }

  .content-left {
    @include fc(14px, #000);
    opacity: 0.5408;
    text-align: center;
  }

  .content-right {
  }
}

.delete-dialog {
  .dialog-title {
    @include wh(540px, 4px);
    background-color: #5a8cff;
    line-height: 4px;
  }

  .dialog-content {
    padding: 80px 75px 50px;

    .delete-tip {
      margin-bottom: 60px;

      .delete-img {
        display: inline-block;
        @include wh(50px, 50px);
        img {
          @include wh(50px, 50px);
        }
      }

      .delete-text {
        display: inline-block;
        padding-bottom: 15px;
        padding-left: 20px;
        vertical-align: bottom;
        @include fc(14px, #666);
      }
    }
    .button-group {
      margin-top: 40px;

      div {
        @include wh(160px, 40px);
        border-radius: 4px;
        border: 1px solid #5a8cff;
        color: #5a8cff;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
      .ok-btn {
        margin-right: 60px;
        background-color: #5a8cff;
        color: #fff;
      }
    }
  }
}
.catename-tip {
  font-size: 12px;
  color: #e7487e;
  padding-left: 80px;
  padding-top: 10px;
}
.catename-tipcontent {
  height: 26px;
}
</style>


<style lang="scss">
@import "src/styles/mixin.scss";

#good-tree {
  .category {
    .el-input {
      width: inherit;
    }
    .el-input__inner {
      border-radius: 0;
      height: 34px;
    }
    .el-input__inner:hover {
      border: 1px solid #bfcbd9;
    }
    .el-input__inner:focus {
      border: 1px solid #bfcbd9;
    }
    .el-input.errtip {
      .el-input__inner {
        border-color: #e7487e;
      }
    }
  }

  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
  .el-checkbox-button {
    margin-right: 10px;
  }
  .el-checkbox-button:first-child .el-checkbox-button__inner,
  .el-checkbox-button:last-child .el-checkbox-button__inner {
    border-radius: 0;
  }
  .el-checkbox-button__inner {
    @include wh(60px, 30px);
    padding: 8px 15px;
    border-left: 1px solid #bfcbd9;
  }
  .el-checkbox-button.is-checked .el-checkbox-button__inner {
    border-color: #5a8cff;
    color: #5a8cff;
    background-color: #fff;
    box-shadow: none;
  }
  .el-checkbox-button.is-checked .el-checkbox-button__inner::after {
    @include wh(12px, 12px);
    position: absolute;
    content: "";
    background-image: url(./images/select.png);
    background-repeat: no-repeated;
    top: 0;
    right: 0;
  }
}
</style>






